import React, {  useContext } from 'react'
import {
  Box,
  Flex,
  IconButton,
  Button,
  Stack,
  useColorMode,
  Avatar,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
} from '@chakra-ui/react'
import { NavLink, useNavigate } from 'react-router-dom'
import { ChevronDownIcon, SunIcon, MoonIcon } from '@chakra-ui/icons'
import Logo from '../Logo'
import { authContext } from '../context/auth-context'

const ThemeToggle = () => {
  const { colorMode, toggleColorMode } = useColorMode()

  return (
    <IconButton
      _hover={{
        color: 'pink.600',
      }}
      aria-label="toggle color mode"
      icon={colorMode === 'light' ? <MoonIcon /> : <SunIcon />}
      onClick={toggleColorMode}
      variant="link"
    />
  )
}

export function HeaderDesktop() {
  const ctx = useContext(authContext)

  const navigate = useNavigate()

  const handleLogout = () => {
    ctx.handleLogout()
    navigate('/')
  }

  return (
    <>
      <Flex flex={{ base: 1 }}>
        <Box p={0} w="4%">
          <NavLink to="/">
            <IconButton
              aria-label="logo"
              variant="link"
              size="md"
              icon={<Logo />}
            />
          </NavLink>
        </Box>
        <Flex ml={10}>
          {/* Left menu wrap */}
          <Stack mt="9px" direction={'row'} spacing={4}>
            {/* Left menu items */}
            <Box>
              <NavLink to="/">Home</NavLink>
            </Box>
            <Box>
              <NavLink to="/test">Test</NavLink>
            </Box>
          </Stack>
        </Flex>
      </Flex>

      {/* Right aligned menu */}
      <Stack justify={'flex-end'} direction={'row'} spacing={6}>
        {!ctx.isLoggedIn ? (
          <>
            <Button
              fontSize={'sm'}
              fontWeight={400}
              variant={'link'}
              _hover={{
                color: 'pink.600',
              }}
              onClick={() => navigate('/login')}
            >
              Sign In
            </Button>
            <Button
              fontSize={'sm'}
              fontWeight={600}
              color={'white'}
              bg={'pink.600'}
              _hover={{
                bg: 'pink.700',
              }}
              onClick={() => navigate('/register')}
            >
              Sign Up
            </Button>
          </>
        ) : (
          <>
            <Button
              variant={'link'}
              fontSize={'sm'}
              fontWeight={400}
              onClick={handleLogout}
              _hover={{
                color: 'pink.600',
              }}
            >
              Log out
            </Button>
            <Menu>
              {({ isOpen, onClose }) => {
                return (
                  <>
                    <MenuButton
                      isActive={isOpen}
                      as={Button}
                      rightIcon={<ChevronDownIcon />}
                      variant="ghost"
                    >
                      <Avatar name="" size="sm" src={ctx.user.avatar} />
                    </MenuButton>
                    <MenuList>
                      <MenuItem onClick={() => navigate('/user')}>
                        Settings
                      </MenuItem>
                      <MenuItem onClick={() => navigate('/changepw')}>
                        Security
                      </MenuItem>
                    </MenuList>
                  </>
                )
              }}
            </Menu>
          </>
        )}
        <ThemeToggle />
      </Stack>
    </>
  )
}
