import React, { useContext, useEffect } from 'react'
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from 'react-router-dom'
import {
  Error,
  Home,
  Login,
  Profile,
  Register,
  Test,
  Account,
  EditProfile,
  ChangePw,
  ForgotPw,
  Header,
  Footer,
  Notifications,
} from './components'
import { authContext } from './context/auth-context'
import {
  Modal,
  ModalOverlay,
  ModalContent,
  useDisclosure,
  CircularProgress,
  Center,
} from '@chakra-ui/react'
import './App.css'

const ProtectedRoute = ({ children }) => {
  const ctx = useContext(authContext)

  if (!ctx.isLoggedIn) {
    return <Navigate to="/home" replace />
  }

  return children
}

function App() {
  const ctx = useContext(authContext)

  const { isOpen, onOpen, onClose } = useDisclosure()

  useEffect(() => {
    console.log('useEffect in App')
    onOpen()
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return ctx.loading ? (
    <>
      <Modal isOpen={isOpen} onClose={onClose}>
        <Center mt="100px">
          <CircularProgress isIndeterminate color="pink.600" />
        </Center>
        <ModalOverlay
          bg="blackAlpha.300"
          backdropFilter="blur(10px) hue-rotate(90deg)"
        />
        <ModalContent id="ModalContent"></ModalContent>
      </Modal>
    </>
  ) : (
    <>
      <Router>
        <Header />
        <Notifications />
        <Routes>
          <Route index element={<Home />} />
          <Route path="home" element={<Home />} />
          <Route path="test" element={<Test />} />
          <Route path="login" element={<Login />} />
          <Route path="register" element={<Register />} />
          <Route
            path="user"
            element={
              <ProtectedRoute>
                <Account />
              </ProtectedRoute>
            }
          />
          <Route
            path="profile"
            element={
              <ProtectedRoute>
                <Profile />
              </ProtectedRoute>
            }
          />
          <Route
            path="edit"
            element={
              <ProtectedRoute>
                <EditProfile />
              </ProtectedRoute>
            }
          />
          <Route
            path="changepw"
            element={
              <ProtectedRoute>
                <ChangePw />
              </ProtectedRoute>
            }
          />
          <Route path="forgot" element={<ForgotPw />} />
          <Route path="*" element={<Error />} />
        </Routes>
        <Footer />
      </Router>
    </>
  )
}

export default App
